.work-orders-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    font-family: 'Arial', sans-serif;
    width: 80%;
    margin-left: 16%;
  }
  
  .work-orders-title {
    font-size: 2rem;
    color: #4a4a4a;
    margin-bottom: 20px;
  }
  
  .create-button {
    background-color: #4a4a4a;
    color: white;
    font-size: 1.2rem;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 20px;
  }
  
  .create-button:hover {
    background-color: #8a8a8a;
  }
  
  .work-orders-table-container {
    width: 100%;
    overflow-x: auto;
  }
  
  .work-orders-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .work-orders-table thead th {
    background-color: #4a4a4a;
    color: white;
    padding: 10px;
    text-align: center;
    font-weight: bold;
  }
  
  .work-orders-table tbody tr {
    border-bottom: 1px solid #e0e0e0;
  }
  
  .work-orders-table tbody tr:nth-child(even) {
    background-color: #f8f8f8;
  }
  
  .work-orders-table tbody tr:hover {
    background-color: #e0e0e0;
  }
  
  .work-orders-table td {
    padding: 10px;
  }
  
  .edit-button {
    background-color: #4a4a4a;
    color: white;
    padding: 5px 10px;
    font-size: 0.9rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .edit-button:hover {
    background-color: #8a8a8a;
  }
  